@import "../../../../assets/styles/__mixins.sass"
@import "../../../../assets/styles/__variables.sass"
@import "../../../../assets/styles/__tegs.sass"

.sign-in
  margin-bottom: 19rem
  margin-right: 2.5rem
  max-width: 23.1rem
  order: 1

  &__title
    font-size: $big-size
    margin-bottom: 0.8em
    text-transform: none

  &__input
    border-radius: 0.25em
    width: 20rem
    padding: 0.7rem
    margin-bottom: 0.825em
    border: 1px solid $light-blue

  &__input_second
    margin-bottom: 1.4em

  &__btn
    padding: .9375rem 8.6rem
    border-radius: 1.775em
    @include button-style()
    background: linear-gradient(180deg, $purple 0%, #8BA4F9 100%)
    font-size: $font-small
    color: $white
    position: relative
    margin-bottom: 2.6em
    letter-spacing: 0.06em

    &::after
      @include arrow-right($color: #ffffff)
      margin-bottom: .3125rem

    &:hover
      @include button-hover()

  &__wrap
    @include flex-between()
    align-items: center

  &__btn_sign-up
    padding: .9375rem 1.25rem
    font-size: $font-small
    text-transform: uppercase
    @include ellipse-button-style-transparent()
    margin-right: 1.2em

    &:hover
      @include button-hover()
